<script>
  let wt = 50
  let ht = 150
  $: currentbmi = (wt / ht / ht) * 10000
  $: minnormwt = ((ht * ht) / 10000) * 17
  $: maxnorwt = ((ht * ht) / 10000) * 24.9
  $: minoverwt = ((ht * ht) / 10000) * 25
  $: maxoverwt = ((ht * ht) / 10000) * 29.9
</script>

<style>
  .bmirow {
    @apply flex justify-between bg-indigo-900 font-semibold mt-4;
  }
</style>

<div class="bg-purple-300 m-2 shadow-2xl rounded-lg">
  <div
    class="flex bg-purple-500 m-1 uppercase text-center tracking-wide border-2
    border-purple-400 rounded">
    BMI measurements
  </div>

  <div class="p-2">
    <div class="px-2 bg-pink-400 flex justify-around">
      <label>Weight</label>
      <div class="my-1">
        <div>
          <input id="weight" type="number" min="0" max="1000" bind:value={wt} />
          <span>kg</span>
        </div>
      </div>
    </div>
    <div class="px-2 bg-pink-500 flex justify-around">
      <label>Height</label>
      <div class="my-1">
        <input id="height" type="number" min="0" max="1000" bind:value={ht} />
        <span>cm</span>
      </div>
    </div>

    <div class="flex-col">
      <div class="bmirow">
        <div>Current BMI :</div>
        <div>{currentbmi ? currentbmi.toFixed(2) : 0}</div>
      </div>
      <p>Normal BMI 17-24.9 : {minnormwt.toFixed(2)}-{maxnorwt.toFixed(2)}</p>
      <p>
        Overweight BMI 25-29.9 : {minoverwt.toFixed(2)}-{maxoverwt.toFixed(2)}
      </p>
    </div>
  </div>
</div>
